@tailwind base;
@tailwind components;
@tailwind utilities;
@import url("https://cdn.jsdelivr.net/gh/orioncactus/pretendard@v1.3.9/dist/web/static/pretendard.min.css");

:root {
  font-family: "Pretendard Variable", Pretendard, -apple-system,
    BlinkMacSystemFont, system-ui, Roboto, "Helvetica Neue", "Segoe UI",
    "Apple SD Gothic Neo", "Noto Sans KR", "Malgun Gothic", "Apple Color Emoji",
    "Segoe UI Emoji", "Segoe UI Symbol", sans-serif;
}

.material-icons {
  font-family: "Material Icons";
  display: inline-block;
}

h1 {
  @apply text-5xl font-extrabold text-black text-left pb-6 break-keep;
}

h2 {
  @apply text-4xl font-bold text-black whitespace-pre-line py-4;
}

h3 {
  @apply text-2xl font-medium text-black whitespace-pre-line leading-relaxed pb-6 break-keep;
}

h4 {
  @apply text-2xl font-extrabold whitespace-pre-line text-black py-2;
}

h5 {
  @apply text-xl font-bold whitespace-pre-line text-orange-600 py-2;
}

p {
  @apply text-xl font-medium text-black leading-relaxed whitespace-pre-line py-1 break-keep;
}

.p-bold {
  @apply font-bold;
}

.p-light {
  @apply font-light;
}

.maintext1 {
  @apply space-y-4;
}

.section-main {
  @apply w-full py-10;
}

.section-main-container {
  @apply max-w-[1200px] mx-auto my-8;
}

/* Desktop에서는 3열, 모바일에서는 1열 */
.grid-container {
  @apply flex-row divide-y divide-neutral-400 space-y-4;
}

.grid-container > .row {
  @apply flex overflow-auto py-4;
}

@media (max-width: 640px) {
  .section-main-container {
    @apply px-4 my-2;
  }

  .grid-container > .row {
    @apply flex-col;
  }
}

.grid-container > .row h5 {
  @apply w-[50px];
}

.grid-container > .row h2 {
  @apply w-[300px];
}

.grid-container > .row p {
  @apply flex-1;
}

/* flex-container: 모바일에서는 flex-col */

.flex-container {
  @apply flex flex-col space-y-6 space-x-0 sm:flex-row sm:space-x-8 sm:space-y-0;
}

.button-bold {
  @apply px-5 py-3 my-4 text-xl font-bold text-white transition duration-300 ease-in-out bg-orange-500 border-2 border-orange-500 rounded-lg hover:bg-white hover:text-orange-500 hover:border-orange-500;
}

.list-light {
  @apply list-disc text-lg font-light space-y-2;
}

.button-tag {
  @apply inline-block border-2 text-lg font-semibold px-5 py-1 rounded-full text-black text-center border-black hover:shadow-lg transition-shadow m-1;
}

.box-rounded {
  @apply px-8 py-6 space-y-2 border rounded-2xl border-neutral-300 bg-neutral-100;
}

@media (max-width: 768px) {
  h1 {
    @apply text-4xl leading-relaxed;
  }
  h2 {
    @apply text-2xl py-2;
  }
  h3 {
    @apply text-xl leading-relaxed;
  }
  h5 {
    @apply text-base py-1;
  }

  p {
    @apply text-lg;
  }
  .p-bold {
    @apply text-lg;
  }
  .p-light {
    @apply text-lg;
  }

  .button-tag {
    @apply px-3 text-sm;
  }
}
